.statsList {
color: #35475a;
margin: 0;
padding: 0;
display: flex;
justify-content: space-between;
    .statsItem {
        list-style: none;
        background: rgb(243, 246n, 249);
        border: 1px solid rgb(232, 236, 242);
        padding-top: 30px;
        height: 88px;
        width: 100%;
        line-height: 1.5;
        display: flex;
        flex-direction: column;
        .label {
            color: #8c9aa8;
        }
        .quantity {
            font-size: 24px;
            font-weight: 600;
            color: darkslategray;
        }
}
}